@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

* a {
  text-decoration: none;
  color: #3a3a3a;
  cursor: pointer;
}

.App {
  display: flex;
  flex-direction: row;
  align-items: space-between;
  justify-content: space-between;
  min-height: 100vh;
  padding: 10vh 3vw;
  background-color: #f7f5f5;
  color: #3a3a3a;
}

.App .title {
  text-align: right;
  padding-right: 3vw;
}

.App button {
  padding: 1em;
  margin: 1em;
  background-color: #fac89f;
  cursor: pointer;
}

.content {
  margin-right: 6vw;
  width: 65vw;
  display: flex;
  justify-content: space-between;
  padding-top: 6vh;
  color: #3a3a3a;
  font-family: "Oswald", sans-serif;
}

.content .title {
  text-align: center;
}

.content .filled {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 3vh;
  text-align: left;
  border-bottom: 3px solid #f6913f;
}

.content .filled h3 {
  font-weight: 200;
  text-align: left;
}

.content .image {
  max-width: 500px;
}

nav {
  margin-left: 3vw;
  min-width: 20vw;
  color: #3a3a3a;
}

nav h2 {
  font-weight: 200;
}

nav hr {
  margin-top: 5px;
  width: 80%;
  border-top: 3px solid #f6913f;
}

nav ol {
  padding: 2vh;
}

nav ol li {
  padding: 8px;
  list-style-type: none;
}

nav ol li .active {
  color: #f6913f;
  margin-left: -0.5em;
}

nav .icon {
  width: 4vmin;
  padding: 5px;
}

.landing h4 {
  font-size: 5rem;
}

.landing .imageBox {
  flex-grow: 1;
  position: relative;
  width: 350px;
  height: auto;
  margin: 20px;
}

.landing .imageBox .image {
  display: block;
  image-rendering: crisp-edges;
  min-height: 100%;
  max-width: 100%;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
}

.gallery {
  display: flex;
  flex-direction: row;
  align-items: space-around;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px;
}

.gallery a {
  font-size: 1.3em;
  text-decoration: none;
  color: whitesmoke;
}

.gallery h3 {
  text-align: center;
}

.gallery .imageBox {
  flex-grow: 1;
  position: relative;
  max-width: 300px;
  height: auto;
  margin: 32px;
}

.gallery .imageBox:hover .overlay {
  opacity: 1;
}

.gallery .imageBox .image {
  display: block;
  image-rendering: crisp-edges;
  min-height: 100%;
  max-width: 100%;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
}

.gallery .imageBox .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  margin: auto;
  opacity: 0;
  transition: 0.5s ease;
  background-color: rgba(0, 0, 0, 0.5);
}

.gallery .imageBox .overlay .text {
  color: white;
  font-size: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.resume {
  flex-direction: column;
  align-items: center;
}

.resume button {
  width: 15vw;
}

.contact {
  min-width: 65vw;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.contact h4 {
  font-size: 5rem;
}

.contact .form {
  margin: 3vw;
}

.contact .textField {
  width: 35vw;
}

.contact .messageField {
  width: 35vw;
}

@media screen and (max-width: 560px) {
  .App {
    flex-direction: column;
  }
  .App .title {
    text-align: center;
  }
  nav ol {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  nav .icon {
    display: none;
  }
  .landing {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    margin: auto;
  }
  .landing .imageBox {
    width: 250px;
  }
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    margin: auto;
  }
  .content .filled {
    width: 90vw;
  }
  .title {
    display: none;
  }
  .contact h4 {
    font-size: 4em;
  }
  .form {
    width: 90vw;
    margin: auto;
  }
  .form .textField,
  .form .messageField {
    width: 80vw;
  }
}
